import { useState } from 'react';
import { __ } from '@wordpress/i18n';
import { usePostAsyncBackgroundMessage } from '../../../iframe/useBackgroundApp';
import { ProxyMessages } from '../../../iframe/integratedMessages';
import { TemplateLabels, TemplateValues, ExcludedTemplateAvailabilityKeys, } from '../../types';
export default function useGetTemplateAvailability() {
    const proxy = usePostAsyncBackgroundMessage();
    const [templateAvailability, setTemplateAvailability,] = useState(null);
    const [availabilityPromise] = useState(() => new Promise(resolve => {
        proxy({
            key: ProxyMessages.GetTemplateAvailability,
            payload: {},
        }).then(data => {
            setTemplateAvailability(data.templateAvailability);
            resolve(data);
        });
    }));
    return { templateAvailability, availabilityPromise };
}
export const getTemplateOptions = (templateAvailability) => {
    if (!templateAvailability) {
        return {};
    }
    return {
        label: __('Templates', 'leadin'),
        options: Object.keys(templateAvailability)
            .filter(templateId => {
            const hubspotFormTemplateAvailability = templateAvailability[templateId];
            return ((hubspotFormTemplateAvailability.canCreateWithMissingScopes ||
                !hubspotFormTemplateAvailability.missingScopes.length) &&
                !Object.values(ExcludedTemplateAvailabilityKeys).includes(templateId));
        })
            .map(templateId => {
            return {
                label: __(TemplateLabels[templateId], 'leadin'),
                value: TemplateValues[templateId],
            };
        }),
    };
};
export function isDefaultForm(value) {
    return Object.values(TemplateValues).includes(value);
}
